
import { useCallback, useEffect, useState } from "react";

export default function useTrivia(){
  const [question, setQuestion] = useState(null); // c—Ç–∞–Ω –ø–∏—Ç–∞–Ω–Ω—è
  const [category, setCategory] = useState('any'); // c—Ç–∞–Ω –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó

  const getQuestion = useCallback(() => { // –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º —Ö—É–∫ –∑–≤–æ—Ä–æ—Ç–Ω—å–æ–≥–æ –≤–∏–∫–ª–∏–∫—É –¥–ª—è –∑–º—ñ–Ω–∏ —É—Ä–ª–∞ –ø–∏—Ç–∞–Ω—å –ø—Ä–∏ –∑–º—ñ–Ω—ñ –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó
    let url = 'https://opentdb.com/api.php?amount=1'; // –æ–≥–ª–æ—à—É—î–º –ø–µ—Ä–µ–º—ñ–Ω–Ω—É —É—Ä–ª –ø–æ –¥–µ—Ñ–æ–ª—Ç—É
    if(category !== 'any') url += `&category=${category}`; // –≤ –≤–∏–ø–∞–¥–∫—É —è–∫—â–æ –≤–∏–±—Ä–∞–Ω–∞ –ø–µ–≤–Ω–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—è –∫–æ–Ω–∫–∞—Ç–µ–Ω—É—î–º –ø–∞—Ä–∞–º–µ—Ç—Ä –∫–∞—Ç–µ–≥–æ—Ä—ñ–π –≤ —É—Ä–ª

    fetch(url) // —Ä–æ–±–∏–º –∑–∞–ø–∏—Ç –ø–æ –∑—ñ–±—Ä–∞–Ω–æ–º—É —É—Ä–ª—É
      .then(res => res.json()) // –æ—á—ñ–∫—É—î–º –≤—ñ–¥–ø–æ–≤—ñ–¥—å –≤—ñ–¥ —Å–µ—Ä–≤–µ—Ä–∞
      .then(data => setQuestion(data.results[0])); // –∑–∞–ø–∏—Å—É—î–º –≤ —Å—Ç–∞–Ω –≤—ñ–¥–ø–æ–≤—ñ–¥—å –∑ —Å–µ—Ä–≤–µ—Ä–∞ —ñ –æ–Ω–æ–≤–ª—é—î–º question 
  }, [category]); // –∑–º—ñ–Ω–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ [category] –∑–∞–ø—É—Å–∫–∞—î –∫–æ–¥ –≤ —Ñ—ñ–≥—É—Ä–Ω–∏—Ö –¥—É–∂–∫–∞—Ö –≤–∏—â–µ

  useEffect(() => { // –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º —Ö—É–∫ –¥–ª—è –ø–µ—Ä–µ—Ä–µ–Ω–¥–µ—Ä—É
    getQuestion();
  }, [getQuestion, category]); // –∑–º—ñ–Ω–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ [category] –∑–∞–ø—É—Å–∫–∞—î –∫–æ–¥ –≤ —Ñ—ñ–≥—É—Ä–Ω–∏—Ö –¥—É–∂–∫–∞—Ö –≤–∏—â–µ

  return {question, category, setCategory, getQuestion}; // –ø–æ–≤–µ—Ä—Ç–∞—î–º –æ–Ω–æ–≤–ª–µ–Ω—ñ –∑–Ω–∞—á–µ–Ω–Ω—è
}